@import url(https://unpkg.com/ionicons@4.2.2/dist/css/ionicons.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

nav.responsive-toolbar {
  background: #333333;
  height: 60px;
  width: 100%;
  position: fixed;
  box-shadow: 2px 0px 2px #ccc; }
  nav.responsive-toolbar ~ * {
    padding-top: 60px; }
  nav.responsive-toolbar ul {
    background: #333333;
    display: flex;
    -webkit-margin-before: 0;
            margin-block-start: 0;
    -webkit-margin-after: 0;
            margin-block-end: 0;
    -webkit-padding-start: 0;
            padding-inline-start: 0;
    height: 100%;
    align-items: center;
    box-shadow: 2px 2px 2px #ccc; }
    nav.responsive-toolbar ul figure {
      -webkit-margin-before: 0;
              margin-block-start: 0;
      -webkit-margin-after: 0;
              margin-block-end: 0;
      -webkit-margin-start: 10px;
              margin-inline-start: 10px;
      -webkit-margin-end: 0;
              margin-inline-end: 0;
      cursor: pointer; }
    nav.responsive-toolbar ul li {
      list-style-type: none;
      padding: 10px 20px; }
      nav.responsive-toolbar ul li .active {
        background: #999; }
    nav.responsive-toolbar ul a {
      color: white;
      text-decoration: none;
      display: flex;
      flex-direction: row-reverse;
      align-items: center; }
    nav.responsive-toolbar ul i {
      margin-right: 5px;
      font-size: 20px; }

@media screen and (max-width: 759px) {
  nav.responsive-toolbar ul {
    height: 100%;
    width: 150px;
    left: -90px;
    position: fixed;
    top: 60px;
    flex-direction: column;
    -webkit-transition: 300ms ease all;
    transition: 300ms ease all; }
    nav.responsive-toolbar ul.active {
      left: 0px; }
    nav.responsive-toolbar ul figure {
      -webkit-margin-start: 2px;
              margin-inline-start: 2px;
      position: fixed;
      left: 9px;
      top: 10px; }
    nav.responsive-toolbar ul li {
      width: 100%;
      padding-left: 0;
      padding-right: 0; }
    nav.responsive-toolbar ul a {
      flex-direction: row;
      justify-content: space-between;
      margin-left: 20px;
      margin-right: 16px; } }

figure {
  -webkit-margin-before: 0;
          margin-block-start: 0;
  -webkit-margin-after: 0;
          margin-block-end: 0;
  -webkit-margin-start: 10px;
          margin-inline-start: 10px;
  -webkit-margin-end: 0;
          margin-inline-end: 0; }

body {
  margin: 0; }

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

